import React, { useContext } from "react";
import SectionContainer from "../../containers/SectionContainer";
import Gallery from "../../components/Gallery";
import { useTranslation } from "react-i18next";
import { makeStyles, useTheme, useMediaQuery } from "@material-ui/core";
import ThemeContext from '../../contexts/themeContext';
import { Canvas } from '@react-three/fiber';
import { Float } from '@react-three/drei'
import { Asset3D, AssetType, Camera3D } from "../../components/Asset3D";
import { OrbitControls } from '@react-three/drei'

const DesignSection = () => {
    const { t } = useTranslation();
    const classes = useStyles();
    const theme = useTheme();
    const { isDarkMode } = useContext(ThemeContext);
    const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
    const isTablet = useMediaQuery(theme.breakpoints.between('sm', 'md'));

    let settings = {};

    if(isTablet) {
        settings.wrapper = {
            position: [-0.06,0.05,0],
            rotation: [0, 0, -Math.PI/6],
        };
        settings.transform = {
            position: [0.0, 0.0, 0.0],
            rotation: [0.0, 0.0, 0.0],
            scale: 0.5,
        };
    } else if(isMobile) {
        settings.wrapper = {
            position: [-0.04,0.05,0],
            rotation: [0, 0, -Math.PI/6],
        };
        settings.transform = {
            position: [0.0, 0.0, 0.0],
            rotation: [0.0, 0.0, 0.0],
            scale: 0.25,
        };
    } else {
        settings.wrapper = {
            position: [-0.05,-0.05,0],
            rotation: [0, 0, -Math.PI/6],
        };
        settings.transform = {
            position: [-0.2, -0.1, 0],
            rotation: [0.0, 0.0, 0.0],
            scale: 0.5,
        };
    }
    
    settings.floating = {
        speed: 2,               // animation speed
        rotationIntensity: 0.4, // how much it rotates
        floatIntensity: 0.05,    // how much it floats vertically
    };

    return (
        <>
            <div className={classes.canvasContainer}>
                <Canvas camera={{ position: [0, 0, 5] }} >
                    <Camera3D fov={10} zoom={1}/>

                    <directionalLight position={[5, 5, 5]} intensity={isDarkMode ? 0.5 : 2} castShadow={false}/>

                    <Float speed={settings.floating.speed} rotationIntensity={settings.floating.rotationIntensity} floatIntensity={settings.floating.floatIntensity}>
                        <group position={settings.wrapper.position} rotation={settings.wrapper.rotation}>
                            <Asset3D type={AssetType.Design} position={settings.transform.position} rotation={settings.transform.rotation} scale={settings.transform.scale} /> 
                        </group>
                    </Float>

                    {/*<OrbitControls 
                        enableZoom={false} 
                        enableRotate={true} 
                        enablePan={false}
                        minPolarAngle={Math.PI / 2}
                        maxPolarAngle={Math.PI / 2}
                    />*/}
                </Canvas>
                <div className={classes.canvasOverlay}></div>
            </div>

            <SectionContainer id="design_section" title={t('menu_design_section')}>
                <Gallery type={"design"} />
            </SectionContainer>
        </>
    );
};

const useStyles = makeStyles((theme) => ({
    canvasContainer: {
        position: "relative",
        height: "200vh",
        width: "100%",
        pointerEvents: "auto",
        marginTop: "0vh",
        [theme.breakpoints.down("sm")]: {
            height: "150vh",
            marginTop: "0vh",
        },
        marginBottom: "-75vh",
        // zIndex: -1,
        //backgroundColor: "red",
    },
    canvasOverlay: {
        position: "absolute",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        pointerEvents: "none", /* Ensure the overlay doesn't block interactions with the canvas */
        /*
        boxShadow: `0 0 100px 100px ${theme.palette.background.default} inset`,
        [theme.breakpoints.down("md")]: {
            height: "70%",
            boxShadow: `0 0 30px 30px ${theme.palette.background.default} inset`,
        },
        [theme.breakpoints.down("sm")]: {
            height: "50%",
            boxShadow: `0 0 20px 20px ${theme.palette.background.default} inset`,
        },
        */
    },
}));

export default DesignSection;
