import React from "react";
import { motion } from "framer-motion";
import DesignSection from "../../sections/DesignSection";
// import { useTheme } from "@material-ui/core";

const Design = () => {
    // const theme = useTheme()
    
    return (
        <motion.main
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{
                type: "spring",
                stiffness: 260,
                damping: 20,
            }}
        >
            <DesignSection />
        </motion.main>
    );
};

export default Design;
